import React from 'react'

import { Panel, Cell, List, PanelHeader, Group, Avatar, platform, ANDROID, Progress, Card, CardScroll, Header, Div, CardGrid } from '@vkontakte/vkui'
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward'
import "./Profile.css"
import Cover from '../Cover/Cover'
import { star, logo } from '../../icons'
import LevelBubble from './LevelBubble/LevelBubble'
import ProfileTabs from './ProfileTabs'
import HistoryList from '../HistoryList'
import TodoCardsList from './ToDoCardList'
import { iModalData, iUser } from '../../interfaces'

import { iApi } from '../../Api'
import TaskCard from './TaskCard/TaskCard'

interface iProfilePage {
    id: string,
    rubrics: any[]
    go: (route: string, meta?: any) => void
    user: iUser
    snackbar: any
    history: any[]
    achieves: any[]
    openModal: (modal: { type: string, data?: iModalData }) => void
    lessons: any[]
    api: iApi
    events: any[]
    fetchEvents: () => Promise<any[]>
}



class Profile extends React.Component<iProfilePage, any> {

    _isMounted: boolean = false

    constructor(props) {
        super(props)

        this.state = {
            tabs: ['rubrics', 'history', 'achieves'],
            selectedTab: 'tasks',
            events: [],
            isLoading: false
        }
    }

    async componentDidMount() {
        this._isMounted = true
        this.setState({ isLoading: true })
        if (this.props.achieves && this.props.achieves.length !== 0) {
            if (!this.state.selectedTab) this.setState({ selectedTab: 'tasks' })
            let tabs = this.state.tabs
            tabs.push('tasks')
            this.setState({ tabs: tabs })
        }

        let events = await this.props.fetchEvents()

        if (this._isMounted) {
            this.setState({ lessons: this.props.lessons, isLoading: false, events: events })
        }
    }

    componentWillUnmount() {
        this._isMounted = false;
    }



    render() {

        let {
            user,
            go,
            rubrics,
            snackbar,
            achieves,
            openModal,
            lessons
        } = this.props




        let cardList = () => {
            return <Group
                header={this.state.isLoading ? <div className="loader" style={{ marginLeft: 'var(--wrapper-padding)', height: 'var(--wrapper-padding-2x)', width: '150px', }}></div> : <Header mode="secondary">Разминки</Header>}
                style={{ marginBottom: 'calc(var(--wrapper-padding-2x) * 3)', marginTop: 'var(--wrapper-padding)' }}
                separator="hide"
            >
                <CardGrid>
                    {(() => {
                        if (this.state.isLoading)
                            return [1, 2].map((el, i) => <Card key={i} size='m' className='loader' ><Div style={{ width: 144, height: 120 }}></Div></Card>)

                        if (lessons && lessons.length !== 0)
                            return lessons.map(lesson => (<TaskCard key={lesson.recID} lesson={lesson} go={go} />))
                    })()}
                </CardGrid>
            </Group>

        }

        let eventsCards = () => {
            let background = (event: any) => {
                if(!event.event_type || !event.event_type['Обложка']) return 'linear-gradient(200.98deg, #485563 -13.11%, #29323C 75.28%)'
                return `url(${event.event_type['Обложка'][0]['url']})`
            }

            return this.state.events.map(event => {
                return <Card
                    size="s"
                    style={{ color: 'white', height: '128px', background: background(event) }}
                    key={event['rec_id']}
                    onClick={() => go('lesson', { lessonID: event.recID, lesson: event, backTo: 'profile' })}
                >
                    <div style={{ width: '110px', padding: '12px' }} >
                        <strong>{event['Рубрика']}</strong>
                        <div style={{ lineHeight: '120%' }}>{event['Name']}</div>
                        <div className="lesson_date">
                            {event['Дата']},<br />{event['Время']} МСК
                        </div>
                    </div>
                </Card >
            })

        }





        return (

            <Panel id='profile'  >
                <PanelHeader></PanelHeader>
                <Cover >
                    <div className="logo-wrapper">
                        <div className="logo" style={{ top: (platform() === ANDROID) ? '25px' : '37px' }}>{logo}</div>
                        <a href="https://vk.com/@lean.school-s-chego-nachat-obuchenie-v-line" target="_blank" className="logo-help">С чего начать</a>
                    </div>

                    <div className="amountContainer">
                        <Avatar src={user.photo_200} size={72}></Avatar>
                        <div className="amountWrapper">
                            <span className="amount">{user['Баланс']}</span>
                            <span className="star">{star('#ffffff')}</span>
                            <br />
                            {`${user.need_exp_to_level_up} XP до ${+user['Уровень'] + 1} уровня`}
                            <Progress value={(user.levelExperience * 100) / (user.next_level["Требует опыта"] - user.current_level['Требует опыта'])} style={{ width: '100%' }} />
                            {/* <a href="https://vk.com/@lean.school-kak-zarabotat-v-line-bally-i-zachem" target="_blank" rel="noopener noreferrer">Зачем баллы?</a> */}
                            <LevelBubble className="levelBubble">{Math.round(user['Уровень'])}</LevelBubble>
                        </div>
                    </div>
                </Cover>

                <ProfileTabs tabs={this.state.tabs} history={this.props.history} selectedTab={this.state.selectedTab} onClickHandler={(tabName) => this.setState({ selectedTab: tabName })} />



                {
                    (() => {
                        if (this.state.selectedTab !== 'tasks') return
                        if (this.state.events.length === 0) return
                        return <Group header={<Header mode="secondary">Встречи</Header>} separator='hide'>
                            <CardScroll>
                                {eventsCards()}
                            </CardScroll>
                        </Group>
                    })()
                }

                {(() => {
                    if (this.state.selectedTab !== 'tasks') return
                    return cardList()
                })()}


                {(() => {

                    if (this.state.selectedTab !== 'achieves') return
                    return <TodoCardsList achieves={achieves} openModal={openModal} user={user} rubrics={rubrics} onButtonClick={go} />
                })()}
                {(() => {
                    if (this.state.selectedTab === 'rubrics') {
                        return <Group title="Рубрики" >
                            <List>
                                {rubrics.map((rubric, i) => {
                                    let cover = () => rubric['Обложка'] ? <Avatar mode="image" src={rubric['Обложка'][0]['url']} size={72} /> : ""
                                    return (<Cell
                                        key={i}
                                        before={cover()}
                                        multiline
                                        description={rubric['desc']}
                                        asideContent={<Icon24BrowserForward fill="var(--icon_secondary)" />}
                                        onClick={(e) => go('rubric', rubric)}
                                    >{rubric['Название']}</Cell>)
                                })}
                            </List>
                        </Group>
                    }
                })()}


                {(this.props.history.length !== 0 && this.state.selectedTab === 'history') ? <HistoryList history={this.props.history} openModal={this.props.openModal} /> : ''}
                {snackbar}


            </Panel>

        )
    }
}



export default Profile